import React, { useState } from 'react'
import Input from '../input/input'
import './login.css'

import { useNavigate } from 'react-router-dom';
import Button54 from '../button-54/button';

import axios from 'axios';
const Login = () => {
  const [data, setData] = useState(null);

  const [file, setFile] = useState(null);
  const [imageUrl, setImageUrl] = useState(null);

  const handleFileChange = (event) => {
    const file = event.target.files[0];
    setFile(file);

    const imageUrl = URL.createObjectURL(file);
    setImageUrl(imageUrl);
  };


  const handleClick = async () => {
    const options = {
      method: 'POST',
      url: 'https://microsoft-computer-vision3.p.rapidapi.com/describe',
      params: {
        language: 'en',
        maxCandidates: '1',
        'descriptionExclude[0]': 'Celebrities'
      },
      headers: {
        'content-type': 'application/json',
    'X-RapidAPI-Key': '06411a468emshd6925bd507c3899p1a9f5bjsn2b0a0b17a641',
    'X-RapidAPI-Host': 'microsoft-computer-vision3.p.rapidapi.com'
      },
      data: {
        url: imageUrl
      }
    };

    try {
      const response = await axios.request(options);
      setData(response.data);
    } catch (error) {
      console.error(error);
      
    }
  };

  return (
    <div>
      <button onClick={handleClick}>Describe Image</button>

      <div>
      <input type="file" onChange={handleFileChange} />
      {imageUrl && 

      <><p>{console.log(imageUrl)}</p>
      
      <img src={imageUrl} alt="Uploaded Image" />
      </>
      }
    </div>

    </div>
  );
};

export default Login